.fullContainer {
    width: 100%;
    margin: 0 auto;

}

#wordGameContainer {
    padding: 3rem 0 3rem 0;
    min-height: 24rem;
}


h1 {
    text-align: center;
}

.alignCenter {
    text-align: center;
    margin: 0 auto;
}



.hoverPush:hover {
    background-color: rgba(255, 255, 255, 1) !important;
    color: purple !important;
}

#gameButtonContainer {
    width: 90%;
    margin: 0 auto;
    max-width: 32rem;
    display: flex;
    justify-content: space-between;
}
#gameButtonContainer>.mainButton{
    max-width: 45%;
}

.fullWidthFlex {
    font-size: 2rem;
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    overflow: hidden;
}

.halfWidth {
    width: 50%;
    flex-shrink: none;
}

.verticalLine {
    width: 0.1rem;
    margin: 0 1rem 0 1rem;
    background-color: black;
}

#maxLivesDiv {
    text-align: right;
}

#maxScoreDiv {
    text-align: left;
}

@-webkit-keyframes spawnWord {
    from {
        opacity: 0;
        -webkit-transform: translateY(-4rem) scale(0.2);
                transform: translateY(-4rem) scale(0.2);
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0) scale(1);
                transform: translateY(0) scale(1);
    }
}

@keyframes spawnWord {
    from {
        opacity: 0;
        -webkit-transform: translateY(-4rem) scale(0.2);
                transform: translateY(-4rem) scale(0.2);
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0) scale(1);
                transform: translateY(0) scale(1);
    }
}

@-webkit-keyframes wordLeave {
    from {
        opacity: 1;
        -webkit-transform: translateY(-4rem) scale(1);
                transform: translateY(-4rem) scale(1);
    }

    to {
        opacity: 0;
        -webkit-transform: translateY(1rem) scale(0);
                transform: translateY(1rem) scale(0);
        display: none;
    }
}

@keyframes wordLeave {
    from {
        opacity: 1;
        -webkit-transform: translateY(-4rem) scale(1);
                transform: translateY(-4rem) scale(1);
    }

    to {
        opacity: 0;
        -webkit-transform: translateY(1rem) scale(0);
                transform: translateY(1rem) scale(0);
        display: none;
    }
}

.wordEnter {
    font-size: 3rem;
    -webkit-animation: spawnWord 1s 1;
            animation: spawnWord 1s 1;
}

.wordLeave {
    font-size: 3rem;
    -webkit-transform: translateY(-4rem);
            transform: translateY(-4rem);
    -webkit-animation: wordLeave 1s forwards;
            animation: wordLeave 1s forwards;
}

.displayContainer {
    margin-top: 1rem;
    height: 8rem;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.displayContainer>h1 {
    width: 100%;
    flex-shrink: 0;
    margin: 0 auto;

}

.fullWidthBlock {
    flex-shrink: 0;
    width: 100%;
    height: 3rem;
    margin: 0;
    padding: 0;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.roundedDiv {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    margin: 0 auto;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border: 0.2rem solid rgba(80, 80, 80, 0.6);
}

.fadeInAnimation {
    -webkit-animation: fadeIn 3s 1;
            animation: fadeIn 3s 1;
}

.flexVertCenter {
    display: flex;
    align-items: center;
}

.flexVertCenter>i {
    font-size: 2.2rem;
    margin: 0 auto;
}

@media only screen and (max-device-width:480px) {
    .fullContainer {
        width: 100%;
        max-width: 100%;
        border-radius: 0;
    }
}

#newGameButton {
    background-color: rgba(210, 220, 200, 0.1);
    outline: none;
    border: 0.1rem solid white;
    color: white;
    padding: 0 2rem 0 2rem;
    height: 2.4rem;
    margin: 0 auto;
    display: flex;
}

.flexToCenter {
    height: 16rem;
    display: grid;
    align-items: center;
}
